import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import {
	Button,
	Card,
	ListGroup,
	ListGroupItem,
	Nav,
	CardGroup,
	CardDeck,
	CardColumns,
	Table
} from "react-bootstrap";
import HMenu from "../../../_metronic/layout/header/HMenu/HMenu"
import Topbar from "../../../_metronic/layout/header/Topbar"
import Footer from "../../../_metronic/layout/footer/Footer"
import {CardActionArea, CardActions, CardContent, CardMedia, Container, Typography} from "@material-ui/core";
import {Link} from "react-router-dom";

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

const useStyles3 = makeStyles({
	card: {
		maxWidth: 345,
		margin: 20
	},
	media: {
		height: 140
	}
});

export default function HomeProject() {
	const classes = useStyles();
	const classes3 = useStyles3();
	return (
		<>
			<div
				className={`kt-header kt-grid__item `}
				id="kt_header"
			>
				<HMenu />
				<Topbar />
			</div>
			
			<div className="kt-space-20" />
			
			<Container fixed>
				<div className={classes.root}>
					<div className="kt-portlet kt-portlet--height-fluid">
						<div className="kt-portlet__head">
							<div className="kt-portlet__head-label">
								<h2 className="kt-portlet__head-title"><strong>Project Page</strong></h2>
							</div>
						</div>
						<div className="kt-portlet__body">
							<div className="kt-widget5">
								<div className="kt-widget5__item ">
									<Grid container spacing={3}>
										<Grid item xs={12} md={6} lg={6}>
											<Link to="/project/create-project">
												<Button
													style={{float: "left",marginTop:20}}
													variant="success"
													size="md"
													// href="/new-project"
												>
													NEW PROJECT
												</Button>
											</Link>
										</Grid>
										<Grid item xs={12} md={6} lg={6}>
											<TextField
												style={{float: "right", maxWidth: 250, marginLeft:10, marginRight: 10}}
												id="keyword"
												label="Search here . . . "
												className={classes.textField}
												type="text"
												autoComplete="current-password"
												margin="normal"
											/>
										</Grid>
										<Grid item md={4} xs={12}>
											<Card className={classes3.card} bg="primary" text="white" style={{textAlign: "center"}}>
												<Card.Body>
													<Card.Title>Project 1</Card.Title>
													<Card.Text>
														Deskripsi Singkat Mengenai Project 1
													</Card.Text>
													<Link to="/detail-project">
														<Button
															variant="outline-light"
															size="md"
														>
															Detail
														</Button>
													</Link>
												</Card.Body>
											</Card>
										</Grid>
										<Grid item md={4} xs={12}>
											<Card className={classes3.card} bg="primary" text="white" style={{textAlign: "center"}}>
												<Card.Body>
													<Card.Title>Project 2</Card.Title>
													<Card.Text>
														Deskripsi Singkat Mengenai Project 2
													</Card.Text>
													<Link to="/detail-project">
														<Button
															variant="outline-light"
															size="md"
														>
															Detail
														</Button>
													</Link>
												</Card.Body>
											</Card>
										</Grid>
										<Grid item md={4} xs={12}>
											<Card className={classes3.card} bg="primary" text="white" style={{textAlign: "center"}}>
												<Card.Body>
													<Card.Title>Project 3</Card.Title>
													<Card.Text>
														Deskripsi Singkat Mengenai Project 3
													</Card.Text>
													<Link to="/detail-project">
														<Button
															variant="outline-light"
															size="md"
														>
															Detail
														</Button>
													</Link>
												</Card.Body>
											</Card>
										</Grid>
										<Grid item xs={12} md={12}>
											<Table striped bordered hover responsive>
											<thead>
											<tr>
												<th>#</th>
												<th>Table heading</th>
												<th>Table heading</th>
												<th>Table heading</th>
												<th>Table heading</th>
												<th>Table heading</th>
												<th>Table heading</th>
											</tr>
											</thead>
											<tbody>
											<tr>
												<td>1</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
											</tr>
											<tr>
												<td>2</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
											</tr>
											<tr>
												<td>3</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
												<td>Table cell</td>
											</tr>
											</tbody>
										</Table>
										</Grid>
									</Grid>
								
								</div>
							</div>
						</div>
					
					</div>
				</div>
			</Container>
			
			<Footer/>
		</>
	);
}
