import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Table} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge, Form} from  "react-bootstrap";
import {Link} from "react-router-dom";


const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

export default class ListRequestAlat extends React.Component{
	
	render() {
		return(
			<Fragment>
				<Container fixed>
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Request Alat</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											<Grid item md={12} xs={12}>
												<div className="kt-section">
													<div className="row">
														<div className="col-md-12">
															<Link to="/site-eng/create-alat">
																<Button variant="success">Tambah</Button>
															</Link>
															
															<div className="col-md-12" style={{marginTop:20}}>
																<Table responsive striped bordered hover>
																	<thead>
																	<tr>
																		<th><strong>No</strong></th>
																		<th><strong>Nomor Request</strong></th>
																		<th><strong>Status</strong></th>
																		<th><strong>Note</strong></th>
																		<th><strong>Action</strong></th>
																	</tr>
																	</thead>
																	<tbody>
																	<tr>
																		<td>1</td>
																		<td>REQ-001-A</td>
																		<td><Badge variant="primary">New</Badge></td>
																		<td>-</td>
																		<td>
																			<Button variant="primary" size="sm" style={{marginRight:5}}>Lihat Detail</Button>
																			<Button variant="danger" size="sm" style={{marginRight:5}}>Hapus</Button>
																		</td>
																	</tr>
																	<tr>
																		<td>2</td>
																		<td>REQ-002-A</td>
																		<td><Badge variant="primary">New</Badge></td>
																		<td>-</td>
																		<td>
																			<Button variant="primary" size="sm" style={{marginRight:5}}>Lihat Detail</Button>
																			<Button variant="danger" size="sm" style={{marginRight:5}}>Hapus</Button>
																		</td>
																	</tr>
																	<tr>
																		<td>3</td>
																		<td>REQ-003-A</td>
																		<td><Badge variant="danger">Reject</Badge></td>
																		<td>Warna Keramik Tidak Sesuai</td>
																		<td>
																			<Button variant="primary" size="sm" style={{marginRight:5}}>Lihat Detail</Button>
																			<Button variant="danger" size="sm" style={{marginRight:5}}>Hapus</Button>
																		</td>
																	</tr>
																	<tr>
																		<td>4</td>
																		<td>REQ-004-A</td>
																		<td><Badge variant="success">Approved</Badge></td>
																		<td>OK</td>
																		<td>
																			<Button variant="primary" size="sm" style={{marginRight:5}}>Lihat Detail</Button>
																			<Button variant="danger" size="sm" style={{marginRight:5}}>Hapus</Button>
																		</td>
																	</tr>
																	</tbody>
																</Table>
															</div>
														
														</div>
													</div>
												</div>
											</Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}