import React,{Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import {Button} from  "react-bootstrap"

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

export default class rencanaAnggaran extends React.Component{
	constructor() {
		super()
		this.state = {
			anggaran:[{nilai:0}],
		}
		this.addAnggaran = this.addAnggaran.bind(this)
		this.removeAnggaran = this.removeAnggaran.bind(this)
	}
	
	addAnggaran = (e) => {
		this.setState((prevState)=>({
			anggaran:[...prevState.anggaran, {nilai:0}]
		}))
	}
	removeAnggaran = (e) => {
		// console.log('a', this.state.anggaran)
		var array = [...this.state.anggaran]
		var index = array.indexOf(e.target.value)
		console.log("arr", array)
		console.log("ind", index)
		if (index === -1) {
			array.splice(index, 1);
			this.setState({anggaran: array});
		}
		// this.setState({
		// 	anggaran.splice(anggaran,1)
		// 	this.setState({anggaran: array})
		// })
	}
	render() {
		let {anggaran} = this.state
		return(
			<Fragment>
				<Container fixed>
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Rencana Anggaran</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
										    <Grid item md={12} xs={12}>
											    <div className="kt-section">
												    <div className="row">
													    <div className="col-md-12">
														    <Button onClick={this.addAnggaran} id="tambah" variant="success">Tambah</Button>
														    {
														    	anggaran.map((val,idx)=>{
														    	    let nilaiId = `nilai-${idx}`
																    let removeId = `remove-${idx}`
																    return (
																    	<div className="row" id={idx}>
																	    {/*<div className="col-md-12">*/}
																		    <div className="col-md-5">
																			    <TextField
																				    id={nilaiId}
																				    label="Nama Anggaran"
																				    name={nilaiId}
																				    margin="normal"
																				    variant="outlined"
																			    />
																		    </div>
																		    <div className="col-md-5">
																			    <TextField
																				    type="number"
																				    id={nilaiId}
																				    label="Nilai Anggaran"
																				    name={nilaiId}
																				    margin="normal"
																				    variant="outlined"
																				    InputProps={{
																					    startAdornment: (
																						    <InputAdornment position="start">Rp.</InputAdornment>
																					    )
																				    }}
																			    />
																		    </div>
																		    <Button variant="danger" id={removeId} rowId={idx}
																		            onClick={(e) => {this.removeAnggaran(e)}}>X</Button>
																	    {/*</div>*/}
																	    </div>
																    )
															    })
														    }
													    </div>
												    </div>
											    </div>
										    </Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}