import React, {useState, useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import {
	Button,
	Card,
	ListGroup,
	ListGroupItem,
	Nav,
	CardGroup,
	CardDeck,
	CardColumns,
	Table
} from "react-bootstrap";
import HMenu from "../../../_metronic/layout/header/HMenu/HMenu"
import Topbar from "../../../_metronic/layout/header/Topbar"
import Footer from "../../../_metronic/layout/footer/Footer"
import {CardActionArea, CardActions, CardContent, CardMedia, Container, Typography} from "@material-ui/core";
import {Link, Redirect} from "react-router-dom";

import "../../../_metronic/_assets/css/tileview.css"

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

const useStyles3 = makeStyles({
	card: {
		maxWidth: 345,
		minHeight: 180,
		margin: 20,
		/*width: "90%",*/
		// display: "flex",
		justifyContent: "center"
	},
	media: {
		height: 140
	}
});


export default function DetailProject(props) {
	const classes = useStyles();
	const classes3 = useStyles3();
	const InitialPage = window.localStorage.getItem("pageName") || ""
	const [pageName, setPageName] = useState("");
	
	useEffect(() => {
		window.localStorage.setItem('pageName', pageName)
	})
	
	console.log('page name', pageName)
	return (
		<>
			{pageName !== "" ? <Redirect to="/dashboard"/>
				// : pageName !== "siteEng" ? <Redirect to="/site-eng/req-mat"/>
					: null}
			<div
				className={`kt-header kt-grid__item `}
				id="kt_header"
			>
				<HMenu />
				<Topbar />
			</div>
			
			<div className="kt-space-20" />
			
			<Container fixed>
				<div className={classes.root}>
					<div className="kt-portlet kt-portlet--height-fluid">
						<div className="kt-portlet__head">
							<div className="kt-portlet__head-label">
								<h2 className="kt-portlet__head-title"><strong>Project Name</strong></h2>
							</div>
						</div>
						<div className="kt-portlet__body">
							<div className="kt-widget5">
								<div className="kt-widget5__item ">
									<Grid container spacing={3}>
										{/*perencanaan*/}
										<Grid item md={3} xs={12}>
											<div className="wrapper" onClick={() => setPageName("perencanaan")}>
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>PE</strong></span>RENCANAAN
													</h2>
												</div>
											</div>
										</Grid>
										<Grid item md={3} xs={12}>
											{/*progress*/}
											<div className="wrapper" onClick={() => setPageName("progress")}>
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>PRO</strong></span>GRESS
													</h2>
												</div>
											</div>
										</Grid>
										<Grid item md={3} xs={12}>
											{/*progress*/}
											<div className="wrapper" onClick={() => setPageName("siteEng")}>
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>SITE</strong></span>ENGINEERING
													</h2>
												</div>
											</div>
										</Grid>
										<Grid item md={3} xs={12}>
											{/*gudang*/}
											<div className="wrapper" onClick={() => setPageName("gudang")}>
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>GU</strong></span>DANG
													</h2>
												</div>
											</div>
										</Grid>
										<Grid item md={3} xs={12}>
											{/*pembelian*/}
											<div className="wrapper" onClick={() => setPageName("logistik")}>
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>LO</strong></span>GISTIK
													</h2>
												</div>
											</div>
										</Grid>
										<Grid item md={3} xs={12}>
											{/*keuangan*/}
											<div className="wrapper" onClick={() => setPageName("keuangan")}>
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>KE</strong></span>UANGAN
													</h2>
												</div>
											</div>
										</Grid>
										<Grid item md={3} xs={12}>
											{/*report*/}
											<div className="wrapper" onClick={() => setPageName("report")}>
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>RE</strong></span>PORT
													</h2>
												</div>
											</div>
										</Grid>
										
										{/*<Grid item md={3} xs={12}>
											<div className="wrapper">
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>Cut</strong></span><strong>out</strong>
													</h2>
												</div>
											</div>
										</Grid>*/}
										
										{/*<Grid item md={3} xs={12}>
											<Card className={classes3.card} bg="primary" text="white" style={{textAlign: "center"}}>
												<Button
													variant="outline-light"
													size="md"
													onClick={() => setPageName("report")}
													style={{
														margin: 5,
														height: "13rem",
														justifyContent: "center"
													}}
												>
													<h3><strong>REPORT</strong></h3>
												</Button>
											</Card>
										</Grid>*/}
										
									</Grid>
								
								</div>
							</div>
						</div>
					
					</div>
				</div>
			</Container>
			
			<Footer/>
		</>
	);
}
